import { Text } from '@chakra-ui/react'

type HighlightTextProps = {
  text: string
  text_color?: string
  text_size?: string
  text_highlight_color?: string
  keywords: string[]
  onKeywordClick: (key: string) => void
  clickOnlyKeyword?: boolean
  keywordsHoverColor?: string
}

export default function HighlightText(props: HighlightTextProps) {
  const {
    text,
    keywords,
    onKeywordClick,
    text_highlight_color = 'text_highlight',
    text_size = '12px',
    text_color = 'primary_gray',
    clickOnlyKeyword = false,
    keywordsHoverColor
  } = props

  const keywordRegex = new RegExp(`(${keywords.join('|')})`, 'gi')
  const parts = text.split(keywordRegex)

  return (
    <Text display="inline" whiteSpace="normal" as="span" textAlign="start">
      {parts.map((part, index) => {
        const isKeyword = keywords.includes(part)
        const isClickable = isKeyword || !clickOnlyKeyword

        return (
          <Text
            key={index}
            as="span"
            fontSize={text_size}
            lineHeight="20px"
            color={isKeyword ? text_highlight_color : text_color}
            _hover={{
              color: isKeyword ? keywordsHoverColor : text_color
            }}
            whiteSpace="inherit"
            cursor={isClickable ? 'pointer' : 'default'}
            onClick={isClickable ? () => onKeywordClick(part) : undefined}
          >
            {part}
          </Text>
        )
      })}
    </Text>
  )
}
